import React from "react"
import { Link } from "gatsby"
import { Container } from '@material-ui/core'
import { Navbar, Nav } from 'react-bootstrap'
import { Helmet } from 'react-helmet'
import background1 from '../assets/main.jpg'
import background2 from '../assets/main-1.jpg'

import SEO from './seo'

const Layout = ({ location, title, children }) => {
  function getBackground() {
    let backgrounds = [background1, background2];
    let random = Math.floor(Math.random() * 2);
    console.log(random);

    return `background: url(${backgrounds[random]}) 0 / cover fixed`
  }

  return (
    <>
      <Helmet bodyAttributes={{ style: getBackground().toString() }} />
      <Navbar bg="light" expand="lg" sticky="top" className="px-5 shadow-sm">
        <Link to="/">
          <Navbar.Brand as="span">PeipeiTu的博客</Navbar.Brand>
        </Link>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Link className="mx-3" to="/">
              <Nav.Link as="span">Home</Nav.Link>
            </Link>
            <Link className="mx-3" to="/notebook">
              <Nav.Link as="span">笔记</Nav.Link>
            </Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
      <SEO title={title} />
      <Container maxWidth="lg" className="pt-5 pb-5">
        {children}
      </Container>
    </>
  )
}

export default Layout
